.container {
  width: 100%;
  height: 15000px;
  .animationContainer {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    .computerContainer {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      .computer {
        position: absolute;
        width: 12rem;
        max-height: 12rem;
      }
      .screen {
        position: absolute;
        display: flex;
        flex-wrap: wrap;
        top: -5rem;
        left: -5.4rem;
        width: 11.2rem;
        height: 6.7rem;
        padding: 0;
        .code {
          display: inline;
          font-size: 0.85rem;
          margin-right: 0.5rem;
          padding-bottom: 0.37rem;
          font-family: Monospace;
          color: #14c18b;

          &:nth-child(1) {
            animation: fadeIn1 1s ease-in infinite;
            animation-play-state: paused;
            animation-delay: calc(var(--scroll) * -1s);
          }
          &:nth-child(2) {
            animation: fadeIn2 1s ease-in infinite;
            animation-play-state: paused;
            animation-delay: calc(var(--scroll) * -1s);
          }
          &:nth-child(3) {
            animation: fadeIn3 1s ease-in infinite;
            animation-play-state: paused;
            animation-delay: calc(var(--scroll) * -1s);
          }
          &:nth-child(4) {
            animation: fadeIn4 1s ease-in infinite;
            animation-play-state: paused;
            animation-delay: calc(var(--scroll) * -1s);
          }
          &:nth-child(5) {
            animation: fadeIn5 1s ease-in infinite;
            animation-play-state: paused;
            animation-delay: calc(var(--scroll) * -1s);
          }
          &:nth-child(6) {
            animation: fadeIn6 1s ease-in infinite;
            animation-play-state: paused;
            animation-delay: calc(var(--scroll) * -1s);
          }
          &:nth-child(7) {
            animation: fadeIn7 1s ease-in infinite;
            animation-play-state: paused;
            animation-delay: calc(var(--scroll) * -1s);
          }
          &:nth-child(8) {
            animation: fadeIn8 1s ease-in infinite;
            animation-play-state: paused;
            animation-delay: calc(var(--scroll) * -1s);
          }
          &:nth-child(9) {
            animation: fadeIn9 1s ease-in infinite;
            animation-play-state: paused;
            animation-delay: calc(var(--scroll) * -1s);
          }
          &:nth-child(10) {
            animation: fadeIn10 1s ease-in infinite;
            animation-play-state: paused;
            animation-delay: calc(var(--scroll) * -1s);
          }
          &:nth-child(11) {
            animation: fadeIn11 1s ease-in infinite;
            animation-play-state: paused;
            animation-delay: calc(var(--scroll) * -1s);
          }
          &:nth-child(12) {
            animation: fadeIn12 1s ease-in infinite;
            animation-play-state: paused;
            animation-delay: calc(var(--scroll) * -1s);
          }
          &:nth-child(13) {
            animation: fadeIn13 1s ease-in infinite;
            animation-play-state: paused;
            animation-delay: calc(var(--scroll) * -1s);
          }
          &:nth-child(14) {
            animation: fadeIn14 1s ease-in infinite;
            animation-play-state: paused;
            animation-delay: calc(var(--scroll) * -1s);
          }
          &:nth-child(15) {
            animation: fadeIn15 1s ease-in infinite;
            animation-play-state: paused;
            animation-delay: calc(var(--scroll) * -1s);
          }
          &:nth-child(16) {
            animation: fadeIn16 1s ease-in infinite;
            animation-play-state: paused;
            animation-delay: calc(var(--scroll) * -1s);
          }
        }
      }
    }
  }

  @keyframes fadeIn1 {
    0% {
      font-size: 3.8rem;
      transform: translate(-30vw, 62vh);
    }
    3% {
      font-size: 2.8rem;
      transform: translate(-10vw, 2vh);
    }
    4%,
    100% {
      font-size: 0.85rem;
      transform: translate(0px, 0px);
    }
  }
  @keyframes fadeIn2 {
    0%,
    2% {
      font-size: 8.8rem;
      transform: translate(10vw, 62vh);
    }
    8%,
    100% {
      font-size: 0.85rem;
      transform: translate(0px, 0px);
    }
  }
  @keyframes fadeIn3 {
    0%,
    6% {
      font-size: 8.8rem;
      transform: translate(-80vw, 10vh);
    }
    12%,
    100% {
      font-size: 0.85rem;
      transform: translate(0px, 0px);
    }
  }
  @keyframes fadeIn4 {
    0%,
    12% {
      font-size: 5rem;
      transform: translate(80vw, 60vh);
    }
    16%,
    100% {
      font-size: 0.85rem;
      transform: translate(0px, 0px);
    }
  }
  @keyframes fadeIn5 {
    0%,
    16% {
      font-size: 8rem;
      transform: translate(50vw, -110vh);
    }
    18% {
      font-size: 7rem;
      transform: translate(48vw, -30vh);
    }
    20%,
    100% {
      font-size: 0.85rem;
      transform: translate(0px, 0px);
    }
  }
  @keyframes fadeIn6 {
    0%,
    18% {
      font-size: 5rem;
      transform: translate(-20vw, 10vh);
    }
    24%,
    100% {
      font-size: 0.85rem;
      transform: translate(0px, 0px);
    }
  }
  @keyframes fadeIn7 {
    0%,
    22% {
      font-size: 5rem;
      transform: translate(50vw, 60vh);
    }
    28%,
    100% {
      font-size: 0.85rem;
      transform: translate(0px, 0px);
    }
  }
  @keyframes fadeIn8 {
    0%,
    26% {
      font-size: 8rem;
      transform: translate(-50vw, 0vh);
    }
    28% {
      font-size: 4rem;
      transform: translate(-30vw, -20vh);
    }
    32%,
    100% {
      font-size: 0.85rem;
      transform: translate(0px, 0px);
    }
  }
  @keyframes fadeIn9 {
    0%,
    28% {
      font-size: 10rem;
      transform: translate(40vw, 50vh);
    }

    36%,
    100% {
      font-size: 0.85rem;
      transform: translate(0px, 0px);
    }
  }
  @keyframes fadeIn10 {
    0%,
    30% {
      font-size: 3.8rem;
      transform: translate(-30vw, 0vh);
    }

    40%,
    100% {
      font-size: 0.85rem;
      transform: translate(0px, 0px);
    }
  }
  @keyframes fadeIn11 {
    0%,
    38% {
      font-size: 2.5rem;
      transform: translate(-20vw, -20vh);
    }
    40% {
      font-size: 1.8rem;
      transform: translate(-10vw, -15vh);
    }
    44%,
    100% {
      font-size: 0.85rem;
      transform: translate(0px, 0px);
    }
  }
  @keyframes fadeIn12 {
    0%,
    42% {
      font-size: 3.5rem;
      transform: translate(5vw, -20vh);
    }
    48%,
    100% {
      font-size: 0.85rem;
      transform: translate(0px, 0px);
    }
  }
  @keyframes fadeIn13 {
    0%,
    40% {
      font-size: 8rem;
      transform: translate(52vw, 60vh);
    }
    46% {
      font-size: 6.5rem;
      transform: translate(42vw, 20vh);
    }
    52%,
    100% {
      font-size: 0.85rem;
      transform: translate(0px, 0px);
    }
  }
  @keyframes fadeIn14 {
    0%,
    48% {
      font-size: 7rem;
      transform: translate(-52vw, 60vh);
    }
    56%,
    100% {
      font-size: 0.85rem;
      transform: translate(0px, 0px);
    }
  }
  @keyframes fadeIn15 {
    0%,
    51% {
      font-size: 3.5rem;
      transform: translate(32vw, 60vh);
    }
    60%,
    100% {
      font-size: 0.85rem;
      transform: translate(0px, 0px);
    }
  }
  @keyframes fadeIn16 {
    0%,
    58% {
      font-size: 7rem;
      transform: translate(2vw, 60vh);
    }
    64%,
    100% {
      font-size: 0.85rem;
      transform: translate(0px, 0px);
    }
  }
}
